<template>
  <div class="minemessage">
    <el-table
      :data="list"
      border=""
      style="width: 100%;"
      v-show="list.length"
      highlight-current-row
    >
      <el-table-column label="复选框" width="100" style="color:red" :render-header="renderHeader">
        <template scope="scope">
          <el-checkbox v-model="scope.row.checked"></el-checkbox>
        </template>
      </el-table-column>
      <el-table-column prop="tradeName" label="商品名称" width="180"></el-table-column>
      <el-table-column prop="price" label="商品价钱" width="180"></el-table-column>
      <el-table-column label="商品数量" width="380">
        <template scope="scope" ref="number">
          <el-input-number
            v-model="scope.row.num"
            @change="handleChange(scope.row.id,scope.row.num)"
            controls-position="right"
            :min="1"
            :max="scope.row.number"
          ></el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="商品总价">
        <template scope="scope">
          <div>{{scope.row.price*scope.row.num}}</div>
        </template>
      </el-table-column>
      <el-table-column label="删除功能">
        <template scope="scope">
          <el-popover placement="top" width="160" v-model="scope.row.remove">
            <p>亲！确定删除此商品吗？</p>
            <div style="text-align: right; margin: 0">
              <el-button size="mini" type="text" @click="scope.row.remove=false">取消</el-button>
              <el-button type="primary" size="mini" @click="removeId(scope.row)">确定</el-button>
            </div>
            <el-button slot="reference">删除</el-button>
          </el-popover>
        </template>
      </el-table-column>
    </el-table>
    <div v-show="list.length==0" style="font-size:20px;color:red;display:none">商品全部为空</div>
    <div>
      总价钱:
      <span>{{countList}}</span>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      list: [],
      count: 0,
      istrue: false,
      arr: []
    };
  },
  computed: {
    //计算总价
    countList: function() {
      var a = 0;
      for (let i = 0; i < this.list.length; i++) {
        if (this.list[i].checked == true) {
          a += this.list[i].price * this.list[i].num;
        }
      }
      this.count = a;
      return this.count;
    }
  },
 // 单选全选
  watch: {
    istrue: function() {
      if (this.istrue == true) {
        for (let k = 0; k < this.list.length; k++) {
          this.list[k].checked = true;
        }
      } else {
        for (let k = 0; k < this.list.length; k++) {
          this.list[k].checked = false;
        }
      }

    }
  },
  created() {
    // this.arr = JSON.parse(localStorage.getItem("cart"));
    // this.list = this.arr;
    this.$root.$on("commodity", value => {
      console.log(value);
      this.getList(value);
    });
  },
  methods: {
      getList(obj) {
        if (this.list.length === 0) {
          this.list.push(obj);
          return;
        }
        //购物车不为空
        var flag = false
        this.list.forEach(item => {
          if (item.id === obj.id) {
            flag = true;
            item.num++;
          }
        });
        if (!flag) {
          this.list.push(obj);
        }
        localStorage.setItem("cart", JSON.stringify(this.list));
      },
      handleChange(id,num) {
        console.log(id)
        this.arr.forEach(item => {
          if (item.id === id && item.num > 1) {
            item.num = num;
            console.log(item.num)
          }
        })
        this.list = this.arr
        localStorage.setItem("cart", JSON.stringify(this.list));
      },
      removeId(value) {
        var ids = value.id;
        for (var i = 0; i < this.list.length; i++) {
          if (ids == this.list[i].id) {
            this.list.splice(i, 1);
          }
        }
      },
    renderHeader: function(h, params) {
      //实现table表头添加
      var self = this;
      return h("div", [
        h(
          "el-checkbox",
          {
            on: {
              change: i => {
                self.istrue = i;
              }
            }
          },
          "全选"
        )
      ]);
    }
  }
};
</script>
<style lang="scss" scoped>
</style>