<template>
  <div class="wrap-materialdetail">
    <div class="materialdetail">
      <ul class="smallImg">
        <li>
          <img src="../assets/img/2.jpg" alt="商品示例" width="100%" height="100%">
        </li>
        <li>
          <img src="../assets/img/2.jpg" alt="商品示例" width="100%" height="100%">
        </li>
        <li>
          <img src="../assets/img/2.jpg" alt="商品示例" width="100%" height="100%">
        </li>
      </ul>
      <div class="bigImg">
        <img :src="detail.imageURL" alt="商品展示">
      </div>
      <div class="decs">
        <h1>{{detail.tradeName}}</h1>
        <p class="text">说明：{{detail.label}}</p>
        <p class="price">{{detail.price}}元</p>
        <el-form ref="form">
          <el-form-item label="商品数量" prop="name">
            <el-input v-model.number="num"></el-input>
          </el-form-item>
        </el-form>
        <p>
          <button @click="addToCart()">加入购物车</button>
          <button class="green">一键购</button>
        </p>
      </div>
    </div>
    <div class="comment">
      <button>评论</button>
    </div>
    <div class="message">
      <div class="textarea">
        <p>
          <span>姓名</span>
          <span>评论日期</span>
        </p>
        <textarea></textarea>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    // var checkAge = (rule, value, callback) => {
    //   if (!value) {
    //     return callback(new Error("不能为空或者0"));
    //   }
    //   setTimeout(() => {
    //     if (!Number.isInteger(value)) {
    //       callback(new Error("请输入数字值"));
    //     } else {
    //       if (value < 0) {
    //         callback(new Error("不能小于0"));
    //       } else {
    //         callback();
    //       }
    //     }
    //   }, 1000);
    // };
    return {
      detail: {
        id: "",
        title: "",
        label: "",
        price: "",
        imageURL: ""
      },
      num: 1,
      checked: false,
      remove: false,
      newDetail: {}
      // rules2: {
      //   age: [{ validator: checkAge, trigger: "blur" }],
      //   price: [{ validator: checkAge, trigger: "blur" }]
      // }
    };
  },
  created() {
    var id = this.$route.params.id;
    this.getData();
  },
  methods: {
    getData() {
      this.$axios.get("/baseTrade/list").then(res => {
        if (res.data.count > 0) {
          let list = res.data.data.filter(
            item => item.id === this.$route.params.id
          );
          this.detail = list[0];
          console.log(this.detail)
        }
      });
    },
    addToCart() {
      let newDetail = {
        ...this.detail,
        num: this.num,
        checked: this.checked,
        remove: this.remove
      };
      this.newDetail = newDetail;
      console.log(this.newDetail)
      this.send();
      //数据清空方法
      this.$refs["form"].resetFields();
      this.$message({
        message: "恭喜你，商品已经成功添加",
        type: "success"
      });
    },
    send() {
      this.$root.$emit("commodity",this.newDetail);
    }
  }
};
</script>

<style lang="scss" scoped>
