    margin-left: 65px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .smallImg {
      width: 95px;
      height: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      img {
        width: 120px;
        height: 120px;
      }
    }
    .bigImg {
      width: 400px;
      height: 400px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .decs {
      width: 373px;
      height: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
