        background-color: #bf0000;
        border-radius: 40px;
        text-align: center;
        color: #ffffff;
        font-weight: 600;
        font-size: 12px;
        margin-right: 37px;
      }
      button:nth-child(2) {
        background-color: green;
      }
    }
  }
  .comment {
    width: 50px;
