      background-color: black;
      color: #fff;
      font-size: 12px;
      font-weight: 600;
    }
  }
  .message {
    width: 100%;
    height: 200px;
    border-top: 1px solid #cccccc;
    .textarea {
      margin-top: 35px;
      p {
        width: 100%;
        height: 34px;
        line-height: 34px;
        span {
          font-size: 16px;
          margin-right: 50px;
        }
      }
      textarea {
