<template>
  <div class="propaganda">
    <el-tabs v-model="activeName" @tab-click="handleClick(activeName)">
      <el-tab-pane label="说明" name="说明">
        <img
          src="../assets/img/img1.jpg"
          alt=""
          width="1000px"
          height="500ox"
          style="margin-top:10px;"
          
        >
      </el-tab-pane>
      <el-tab-pane label="菜单" name="菜单" class="poster">
        <div class="first">
          <div class="two" v-for="item in list" :key="item.id">
            <img :src="item.image" alt="" width="100%" height="100%" @click="go(item.id)">
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="传单" name="传单" class="poster">
        <div class="first">
          <div class="two" v-for="item in list" :key="item.id">
            <img :src="item.image" alt="" width="100%" height="100%" @click="go(item.id)">
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="海报" name="海报" class="poster">
        <div class="first">
          <div class="two" v-for="item in list" :key="item.id">
            <img :src="item.image" alt="" width="100%" height="100%" @click="go(item.id)">
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  name: "Propaganda",
  data() {
    return {
      activeName: "说明",
      list: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    handleClick(activeName) {
      console.log(this.activeName);
      this.getData(activeName);
    },
    go(id) {
      this.$router.push("/menudetail/" + id);
    },
    getData(activeName) {
      this.$axios
        .get("/template/list", {
          params: {
            templateType: activeName
          }
        })
        .then(res => {
          console.log(res);
          this.list = res.data.data;
          console.log(this.list.length);
        });
    }
  }
};
</script>

<style lang="scss">
.first .two {
  height: 250px;
  img {
    margin: 20px;
  }
}
.first div {
  display: inline-block;
  height: 200px;
  width: 300px;
  overflow: hidden;
}
.poster .first {
  position: relative;
  height: 1000px;
  width: 1000px;
}

.propaganda {
  height: 1000px;
  margin-top: 50px;
}
.imgs img {
  margin-right: 15px;
  margin-bottom: 15px;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
</style>
