<template>
  <div class="common">
    <el-row :gutter="20" class="block">
      <!-- 轮播 -->
      <el-col :span="16">
        <div class="banner">
          <el-carousel trigger="click">
            <el-carousel-item v-for="item in list" :key="item.id">
              <h3 class="small">
                <img :src="item.banner_img">
              </h3>
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-col>
      <!-- 轮播旁的图片 -->
      <el-col :span="8">
        <div class="grid-content bg-purple">
          <el-row :gutter="20" class="banner-right" v-for="item in smallImg" :key="item.id">
            <el-col :span="12">
              <div class="grid-content bg-purple-dark">
                <img :src="item.smallImage_img" alt="">
              </div>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <!-- 最新文章 -->
    <div class="news">
      <NewsTemplate/>
    </div>
  </div>
</template>

<script>
import NewsTemplate from "@/components/NewsTemplate";

export default {
  name: "Home",
  components: {
    NewsTemplate
  },
  data() {
    return {
      list: [],
      smallImg: []
    };
  },
  created() {
    this.getData();
    this.getSmallImg();
  },
  methods: {
    getData() {
      this.$axios.get("/home/getBannerShowList").then(res => {
        this.list = res.data.data;
        console.log(this.list);
      });
    },
    getSmallImg() {
      this.$axios.get("/home/getSmallImageShowList").then(res => {
        this.smallImg = res.data.data;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.common {
  .block {
    height: 300px;

    .banner {
      border-radius: 4px;
    }

    .banner-right {
      width: 56%;
      height: 145px;
      margin-bottom: 10px;
    }
  }
}

.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: red;
}
.grid-content {
  height: 145px;
  border-radius: 4px;
  min-height: 36px;
  float: left;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  img {
    width: 217%;
    height: 100%;
  }
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}

// 走马灯
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>
