<template>
  <div class="formula-show">
    <el-row>
      <el-col :span="7" :offset="1" v-for="item in list" :key="item.id" class="formula-picture mb">
        <div class="produce-img mb">
          <img :src="item.imageURL" alt />
        </div>
        <div class="product-info">
          <p class="product-detail" @click="go(item.id)">
            <router-link :to="{name: 'home'}">{{item.tradeName}}</router-link>
          </p>
          <!-- <p class="product-detail">
                        <router-link :to="{name: 'home'}">{{list.label}}</router-link>
          </p>-->
          <p class="product-price color1">
            <span>{{item.price}} 元</span>
            <!-- <span><i class="el-icon-plus"></i>确认购买</span> -->
          </p>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-pagination
        background
        layout="prev, pager, next"
        :page-size="8"
        :total="list.length"
        :hide-on-single-page="value"
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "MaterialShow",
  data() {
    return {
      value: true,
      list:[]
    }
  },
  created() {
      this.getData()
  },
  methods: {
    go(id) {
      this.$router.push("/materialdetail/" + id);
    },
    getData() {
      this.$axios.get("/baseTrade/list").then(res => {
        if (res.data.count > 0) {
            console.log(res)
          this.list = res.data.data;
        }
      });
    }
  }
};
</script>

<style lang="scss">
.formula-show {
  .formula-picture {
    .produce-img {
      height: 300px;

      img {
        width: 100%;
        height: 100%;
      }
    }

    .product-detail a {
      font-size: 16px;
      color: #373737;
      line-height: 25px;
    }

    .product-price {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 14px;
        line-height: 30px;
      }
    }
  }
}
.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #bf0000;
}
.el-pagination.is-background .el-pager li:not(.disabled):hover {
  color: #bf0000;
}
</style>