<template>
  <div>
    <el-row :gutter="20">
      <el-col :span="8" v-for="item in list" :key="item.id">
        <router-link :to="{name: 'home'}" class="template-img">
          <video
            width="300px"
            height="300px"
            :src="item.videoUrl"
            controls="controls"
            loop="loop"
            :poster="item.cover"
          ></video>
        </router-link>
        <router-link :to="{name: 'home'}" class="template-title">{{item.content}}</router-link>
        <p class="template-detail mb">{{item.introduction}}</p>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "Teaching",
  data() {
    return {
      list: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    go(id) {
      this.$router.push("/materialdetail/" + id);
    },
    getData() {
      this.$axios.get("/menu/queryTeachVideoList?page=1&limit=10").then(res => {
        console.log(res);
        if (res.data.count > 0) {
          this.list = res.data.data;
        }
      });
    }
  }
};
</script>

<style lang="scss">
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }

  .template-img {
    display: inline-block;
    height: 180px;
    position: relative;

    img {
      width: 100%;
      height: 100%;
    }

    span {
      display: inline-block;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background-color: black;
      opacity: 0.7;
      text-align: center;
      line-height: 48px;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }

    .el-icon-caret-right:before {
      font-size: 30px;
      color: #fff;
    }
  }

  .template-title {
    display: inline-block;
    color: #262626;
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 4px;
    margin-top: 10px;
    max-height: 48px;
  }

  .template-detail {
    color: #787878;
    font-size: 13px;
    line-height: 18px;
    max-height: 36px;
  }
}
</style>
